import React from 'react';
import PageHeader from '../component/PageHeader';
import PostPrev from '../component/PostPrev';
import { firebaseStorage } from '../config';
//6 
const Home = () => {
    return (
        <div>
            <PageHeader title="   " subtittle="   ." Background={firebaseStorage.Url + "Home.jpg?alt=media&token=" + firebaseStorage.Token} />
            <div className="container">
                <div className="row">
                    <div class="col-lg-8 col-md-10 mx-auto">
                        <PostPrev writer="nogada" postTitle="   " postSubtitle="  ?" />
                        <PostPrev writer="nogada" postTitle="   " postSubtitle="   ...." />
                        <PostPrev writer="nogada" postTitle=" " postSubtitle="   ... ..." />
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Home;


